import { HttpStatus, Injectable, Logger } from '@nestjs/common';
import { ConfigService } from '@nestjs/config';
import { JwtService } from '@nestjs/jwt';
import { from, of } from 'rxjs';
import { catchError, filter, map, switchMap, tap } from 'rxjs/operators';
import { throwException } from '@shared/exception';
import { HttpService } from '@nestjs/axios';
import { EventEmitter2 } from '@nestjs/event-emitter';
import { InjectModel } from '@nestjs/sequelize';
import { OAuth2Client } from 'google-auth-library';
import { CacheMessageAction, CacheManagerService } from '@shared/cache-manager';
import { GitHubConfig, GoogleConfig } from '@shared/configs';
import {
  CreateAccountDto,
  SignInDto,
  SignInOauth,
  AuthenticateDto,
  ResendVerifyEmail,
  VerifyEmailOtp,
} from '@shared/dtos/account';
import { Account } from '@shared/models/account';
import { HttpStatusCode } from 'axios';
import { BcryptService } from 'shared/bcrypt/src/bcrypt.service';
import {
  AccountVerifyStatusEnum,
  CredentialTypeEnum,
  GitHubUser,
} from '@shared/types';
import { AccountService } from '../account/account.service';
import { DefaultProfileValue } from '@shared/models/profile';

@Injectable()
export class AuthService {
  oauthClient!: OAuth2Client;
  githubConfig!: GitHubConfig;
  googleConfig!: GoogleConfig;

  constructor(
    @InjectModel(Account)
    private readonly accountModel: typeof Account,
    private readonly jwtService: JwtService,
    private readonly httpService: HttpService,
    private readonly cacheService: CacheManagerService,
    private readonly eventEmitter: EventEmitter2,
    private readonly bcryptService: BcryptService,
    private readonly configService: ConfigService,
    private readonly accountService: AccountService
  ) {
    this.githubConfig = this.configService.get<GitHubConfig>('github');
    this.googleConfig = this.configService.get<GoogleConfig>('google');
    this.oauthClient = new OAuth2Client({
      clientId: this.googleConfig?.clientId,
    });
  }

  // eslint-disable-next-line @typescript-eslint/no-wrapper-object-types
  generateFullTokens<T>(payload: T & Object) {
    return of(payload).pipe(
      map((payload) => ({
        accessToken: this.jwtService.sign(payload, {
          expiresIn: '2m',
        }),
      })),
      tap((token) => Logger.log('accessToken: ', token?.accessToken)),
      map(({ accessToken }) => ({
        accessToken,
        refreshToken: this.jwtService.sign(payload, {
          expiresIn: '30d',
        }),
      })),
      tap((token) => Logger.log('refreshToken: ', token.refreshToken)),
      catchError((error) =>
        throwException(500, `Li to token ${error.message}`)
      )
    );
  }

  // eslint-disable-next-line @typescript-eslint/no-wrapper-object-types
  generateAccessTokens<T>(payload: T & Object) {
    return of(payload).pipe(
      map((payload) => ({
        accessToken: this.jwtService.sign(payload, {
          expiresIn: '2m',
        }),
      })),
      catchError((error) =>
        throwException(500, `Li to token ${error.message}`)
      )
    );
  }

  private verifyToken(token: string) {
    return from(this.jwtService.verifyAsync(token)).pipe(
      catchError(() =>
        throwException(401, `Token khng hp l hoc  ht hn!`)
      )
    );
  }

  handleVerifyEmail(body: VerifyEmailOtp) {
    return this.accountService.handleVerifyEmail(body);
  }

  handleSendTokenVerifyEmail(body: ResendVerifyEmail) {
    return this.accountService.getExistingAccount(body.email).pipe(
      switchMap((existingUser) => {
        if (!existingUser) {
          return throwException(
            HttpStatus.NOT_FOUND,
            'Ti khon khng tn ti, vui lng th li vi email khc.'
          );
        }

        if (existingUser && !existingUser.isVerify) {
          const key = `${AccountVerifyStatusEnum.UNVERIFY}#${body.email}`;
          return this.cacheService.get(key).pipe(
            switchMap((cacheData) => {
              if (cacheData) {
                return throwException(
                  HttpStatusCode.BadRequest,
                  'Vui lng th li sau t pht.'
                );
              }

              return of({
                message: `ng dn xc thc ti khon  c gi n email: ${body.email}. Vui lng kim tra hp th  hon tt qu trnh xc thc ti khon.`,
              });
            }),
            tap(() => {
              this.accountService.handleSendTokenVerifyEmail(body.email);
            })
          );
        }

        return throwException(
          HttpStatus.BAD_REQUEST,
          'Ti khon ny  c xc thc, vui lng th li vi email khc. '
        );
      })
    );
  }

  handleSignUp({ email, password, confirmPassword }: CreateAccountDto) {
    return this.accountService.getExistingAccount(email).pipe(
      switchMap((existingUser) => {
        if (existingUser && existingUser?.isVerify) {
          return throwException(
            HttpStatus.BAD_REQUEST,
            'Ti khon  tn ti, vui lng th li vi email khc.'
          );
        }

        if (existingUser && !existingUser?.isVerify) {
          return throwException(
            HttpStatus.BAD_REQUEST,
            'Ti khon  tn ti nhng cha xc thc, xin vui lng xc thc  ng nhp.'
          );
        }
        return this.accountService.handleCreateAccount({
          email: email,
          password: password,
          confirmPassword,
        });
      })
    );
  }

  handleSignIn({ email, password }: SignInDto) {
    return this.accountService.getExistingAccount(email).pipe(
      switchMap((userData) => {
        if (userData && userData.isVerify) {
          return throwException(
            HttpStatus.BAD_REQUEST,
            'Ti khon cha c xc thc, xin vui lng xc thc  ng nhp.'
          );
        }

        if (userData && userData.isVerify) {
          return this.bcryptService
            .comparePassword(password, userData.password)
            .pipe(
              switchMap((isMatch) => {
                if (!isMatch) {
                  return throwException(
                    HttpStatus.BAD_REQUEST,
                    'Mt khu khng chnh xc.'
                  );
                }
                delete userData.password;
                return from(
                  this.generateFullTokens<{
                    id: string;
                    email: string;
                    createdAt: string;
                    updatedAt: string;
                  }>(userData)
                ).pipe(
                  map((tokens) => ({
                    message: 'ng nhp thnh cng.',
                    data: {
                      ...userData,
                      tokens,
                    },
                  }))
                );
              })
            );
        }

        return throwException(
          HttpStatusCode.NotFound,
          'Khng tm thy ngi dng.'
        );
      })
    );
  }

  handleSignInWithToken({ token }: AuthenticateDto) {
    return this.verifyToken(token).pipe(
      switchMap(() => {
        const source = this.jwtService.decode(token) as { email: string };
        if (!source?.email) {
          return throwException(
            HttpStatusCode.NotFound,
            'Khng tm thy ngi dng.'
          );
        }
        return this.accountService.getExistingAccount(
          source.email,
          CredentialTypeEnum.NONE
        );
      })
    );
  }

  handleOAuth({ token, credentialType }: SignInOauth) {
    switch (credentialType) {
      case CredentialTypeEnum.GITHUB:
        return this.handleOAuthGithub({ token, credentialType });
      default:
        return this.handleOAuthGoogle({ token, credentialType });
    }
  }

  private handleOAuthGoogle({ token }: SignInOauth) {
    console.log('handleOAuthGoogle: ', token);
    return from(
      this.oauthClient.verifyIdToken({
        idToken: token,
        audience: this.googleConfig.clientId,
      })
    ).pipe(
      tap((response) => {
        console.log('Verify google sign in response: ', response);
      }),
      switchMap((response) => {
        // response.??
        return of(1);
        return this.accountService
          .getExistingAccount('', CredentialTypeEnum.GITHUB)
          .pipe(
            switchMap((existingAccount) => {
              if (existingAccount) {
                delete existingAccount.password;
                return this.generateFullTokens(existingAccount).pipe(
                  map((tokens) => ({
                    message: 'ng nhp thnh cng.',
                    data: { ...existingAccount, tokens },
                  }))
                );
              }

              return this.createNewAccountAndProfile({
                email: '',
                name: '',
                avatarUrl: 'avatar_url',
                credentialType: CredentialTypeEnum.GITHUB,
                bio: '',
                githubLink: 'html_url',
              });
            })
          );
      })
    );
  }

  private handleOAuthGithub({ token }: SignInOauth) {
    const { client_id, client_secret, url } = this.githubConfig;

    const payload = {
      client_id,
      client_secret,
      code: token,
      accept: 'json',
    };

    return this.httpService.post(url, payload).pipe(
      filter((response) => !!response?.data),
      map((response) => {
        const tokenMatch = (response?.data as string)?.match(
          /access_token=([^&]*)/
        )?.[1];
        if (!tokenMatch) {
          return throwException(
            HttpStatus.BAD_REQUEST,
            'Li xc thc ngi dng github, vui lng th li.'
          );
        }
        return tokenMatch;
      }),
      switchMap((token: string) => {
        Logger.log('Received Token:', token);
        return this.getGithubUserInfo(token);
      }),
      switchMap((userInfo) =>
        this.handleGetOrCreateGithubAccount(userInfo.data)
      ),
      catchError((error) => {
        Logger.error('Error during OAuth sign-in:', error);
        return throwException(
          HttpStatus.INTERNAL_SERVER_ERROR,
          'C li xy ra trong qu trnh xc thc ngi dng t github.'
        );
      })
    );
  }

  private getGithubUserInfo(token: string) {
    return this.httpService
      .get<GitHubUser>(this.githubConfig.userInfoUrl, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .pipe(
        catchError((error) => {
          Logger.error('Error fetching GitHub user info:', error);
          return throwException(
            HttpStatus.BAD_REQUEST,
            'Ly thng tin ngi dng t github tht bi'
          );
        })
      );
  }

  private handleGetOrCreateGithubAccount(userInfo: GitHubUser) {
    const { email, name, avatar_url, bio, login, html_url } = userInfo;
    Logger.log('GitHubUser: ', userInfo);

    const _email = email || login + '@github.com';

    return this.accountService
      .getExistingAccount(_email, CredentialTypeEnum.GITHUB)
      .pipe(
        switchMap((existingAccount) => {
          if (existingAccount) {
            delete existingAccount.password;
            return this.generateFullTokens(existingAccount).pipe(
              map((tokens) => ({
                message: 'ng nhp thnh cng.',
                data: { ...existingAccount, tokens },
              }))
            );
          }

          return this.createNewAccountAndProfile({
            email: _email,
            name,
            avatarUrl: avatar_url,
            credentialType: CredentialTypeEnum.GITHUB,
            bio,
            githubLink: html_url,
          });
        })
      );
  }

  private createNewAccountAndProfile({
    email,
    name,
    bio,
    avatarUrl,
    credentialType,
    githubLink,
  }: {
    email: string;
    name: string;
    avatarUrl: string;
    credentialType: string;
    bio: string;
    githubLink: string;
  }) {
    let accountData: any;
    return from(
      this.accountModel.create({ email, credentialType, isVerify: true })
    ).pipe(
      map((account) => {
        accountData = account.toJSON();
        return accountData;
      }),
      switchMap((account) =>
        this.createProfile({
          accountId: account.id,
          fullName: name,
          avatarUrl,
          bio,
          githubLink,
        }).pipe(
          tap((profile) => {
            const key = this.getCacheKey(email, CredentialTypeEnum.GITHUB);
            this.eventEmitter.emit(CacheMessageAction.Create, {
              key,
              value: { ...accountData, email, profile },
            });
          }),
          switchMap((profile) =>
            this.generateFullTokens({
              ...accountData,
              fullName: profile.fullName,
            }).pipe(
              tap(() => {
                delete accountData.password;
              }),
              map((tokens) => ({
                message: 'ng nhp thnh cng.',
                data: { ...accountData, tokens, profile },
              }))
            )
          )
        )
      )
    );
  }

  private createProfile({
    accountId,
    fullName,
    avatarUrl,
    bio,
    githubLink,
  }: {
    accountId: string;
    fullName: string;
    avatarUrl: string;
    bio: string;
    githubLink: string;
  }) {
    return from(
      this.accountService.handleCreateProfile(
        {
          fullName,
          avatarUrl,
          bio,
          githubLink,
        },
        accountId
      )
    ).pipe(
      catchError((error) => {
        Logger.error('Error creating profile:', error);
        return throwException(
          HttpStatus.INTERNAL_SERVER_ERROR,
          'To thng tin ngi dng tht bi.'
        );
      })
    );
  }

  private getCacheKey(
    email: string,
    credentialType: CredentialTypeEnum = CredentialTypeEnum.NONE
  ) {
    return `ACCOUNT#${email}#${credentialType}`;
  }
}
